import React, { useEffect, useState } from 'react';
import { useNavigate } from "react-router-dom";
import { Box, Button, Link, TextField, Toolbar, Typography } from "@mui/material";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import Header from '../components/Header';
import Auth from "../util/auth";
import { errorToastOptions } from "../util/toast.options";

const styleBody = {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    height: "100vh"
};

const styleForm = {
    display: "flex",
    flexDirection: "column",
    gap: "20px",
    width: "500px",
    padding: "20px 10px",
    borderRadius: "10px",
};


const Registration = () => {
    const navigate = useNavigate();
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");

    useEffect(() => {
        document.title = "";
        if (localStorage.getItem("auth")) navigate("/");
    }, [navigate]);

    const checkPasswordComplexity = (password) => {
        const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[~!@#$;%^:&?*()\-=+|/<>])[A-Za-z\d~!@#$;%^:&?*()\-=+|/<>]{8,}$/;
        const requirements = {
            lowercase: /(?=.*[a-z])/,
            uppercase: /(?=.*[A-Z])/,
            digit: /(?=.*\d)/,
            special: /(?=.*[~!@#$;%^:&?*()\-=+\|/<>])/
        };

        const missingRequirements = [];

        if(password.length < 8){
            const errorMessage = "";
            return {valid: false, message: errorMessage};
        }

        if (!requirements.lowercase.test(password)) {
            missingRequirements.push(" ");
        }
        if (!requirements.uppercase.test(password)) {
            missingRequirements.push(" ");
        }
        if (!requirements.digit.test(password)) {
            missingRequirements.push("");
        }
        if (!requirements.special.test(password)) {
            missingRequirements.push(" ");
        }

        console.log(missingRequirements);

        if (missingRequirements.length > 0) {
            const errorMessage = `  : ${missingRequirements.join(", ")}`;
            return { valid: false, message: errorMessage };
        }
    
        return { valid: passwordRegex.test(password), message: "" };
    };

    const registration = (event) => {
        event.preventDefault();

        const passwordComplexity = checkPasswordComplexity(password);

        if (!passwordComplexity.valid) {
            const errorMessage = "    .<br>" + passwordComplexity.message;
            if (toast.isActive("error-toast")) {
                toast.update("error-toast", {
                    render: <div dangerouslySetInnerHTML={{ __html: errorMessage }} />,
                    ...errorToastOptions,
                });
            } else {
                toast.error(<div dangerouslySetInnerHTML={{ __html: errorMessage }} />, {
                    toastId: "error-toast",
                    ...errorToastOptions,
                });
            }
            return;
        }

        const registrationData = { username, password };
        const registrationPromises = Auth.registration(registrationData);

        registrationPromises
            .then(({ data }) => {
                navigate("/login");
            })
            .catch((err) => {
                const { message } = err.response.data;
                if (err.response.status === 403) {
                    navigate("/");
                }
                if (toast.isActive("error-toast")) {
                    toast.update("error-toast", {
                        render: message,
                        ...errorToastOptions,
                    });
                } else {
                    toast.error(message, {
                        toastId: "error-toast",
                        ...errorToastOptions,
                    });
                }
            });
    };

    return (
        <Box>
            <Header isAuth={false} />
            <Box sx={styleBody}>
                <Box sx={styleForm} component="form" onSubmit={registration}>
                    <Toolbar sx={{ margin: "auto", fontSize: "40px" }} component="h1">
                        
                    </Toolbar>
                    <TextField name="username"
                        type="text"
                        label="Username"
                        variant="outlined"
                        required={true}
                        autoFocus={true}
                        onChange={(e) => setUsername(e.target.value)} />
                    <TextField name="password"
                        type="password"
                        label="Password"
                        variant="outlined"
                        required={true}
                        onChange={(e) => setPassword(e.target.value)} />
                    <Button sx={{ fontWeight: "bold", fontSize: "24px" }}
                        variant="contained"
                        color="primary"
                        type="submit">
                        
                    </Button>
                    <Link sx={{ fontSize: "20px" }} href="/login">
                        
                    </Link>
                </Box>
            </Box>
            <ToastContainer />
        </Box>
    );
};

export default Registration;