import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Box, Toolbar, Button, TextField, Typography, Link, Container } from "@mui/material";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import Header from '../components/Header';
import Auth from "../util/auth";
import { errorToastOptions } from "../util/toast.options";

const styleBody = {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    height: "100vh"
};

const styleForm = {
    display: "flex",
    flexDirection: "column",
    gap: "20px",
    width: "500px",
    padding: "20px 10px",
    borderRadius: "10px",
};

const Login = () => {
    const navigate = useNavigate();
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");

    useEffect(() => {
        document.title = "";
        if (localStorage.getItem("auth")) navigate("/");
    }, [navigate]);


    const handleLogin = (event) => {
        event.preventDefault();
        const loginData = { username, password };
        const loginPromises = Auth.login(loginData);

        loginPromises
            .then(({ data }) => {
                const { user } = data;
                localStorage.setItem("auth", user.username);
                navigate("/");
            })
            .catch((err) => {
                const { message } = err.response.data;
                if (err.response.status === 403) {
                    navigate("/");
                }
                if (toast.isActive("error-toast")) {
                    toast.update("error-toast", {
                        render: message,
                        ...errorToastOptions,
                    });
                } else {
                    toast.error(message, {
                        toastId: "error-toast",
                        ...errorToastOptions,
                    });
                }
            });
    };

    return (
        <Container>
            <Header isAuth={false} />
            <Box sx={styleBody}>
                <Box sx={styleForm} component="form" onSubmit={handleLogin}>
                    <Toolbar sx={{ margin: "auto", fontSize: "40px" }} component="h1">
                        
                    </Toolbar>
                    <TextField name="username"
                        type="text"
                        label="Username"
                        variant="outlined"
                        required={true}
                        autoFocus={true}
                        onChange={(e) => setUsername(e.target.value)} />
                    <TextField name="password"
                        type="password"
                        label="Password"
                        variant="outlined"
                        required={true}
                        onChange={(e) => setPassword(e.target.value)} />
                    <Button sx={{ fontWeight: "bold", fontSize: "24px" }}
                        variant="contained"
                        color="primary"
                        type="submit">
                        
                    </Button>
                    <Link sx={{ fontSize: "20px" }} href="/registration">
                        
                    </Link>
                </Box>
            </Box>
            <ToastContainer />
        </Container>
    );
};

export default Login;